.headerBackground {
  height: 10vh;
  display: flex;
  justify-content: space-between;
}

.hambergerBox {
  width: 7vh;
  height: 10vh;
  margin-left: 2vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hambergerCircle {
  width: 6vh;
  height: 6vh;
  border-radius: 2vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hambergerCircleOver {
  width: 6vh;
  height: 6vh;
  border-radius: 2vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(186, 186, 186);
  cursor: pointer;
}

.hembergerIcon {
  width: 4vh;
  height: 4vh;
  font-size: 4vh;
}

.ProfileDiv {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 2vw;
}
