.HomeBox {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
}

.CommitteeListBox {
  width: 70vw;
  height: 80vh;
  background-color: white;
  border-radius: 1vh;
  box-shadow: 0 2px 6px 0 hsla(0, 0%, 0%, 0.2);
}

.CommitteeListTextBottom {
  width: 25vw;
  height: 0.5vh;
  margin-left: 0.5vh;
  margin-bottom: 1vh;
  background-color: #95bdfd;
}

.CommitteeTeamBox {
  width: 100%;
  display: flex;
}
